import React from "react";
import UserItem from "./UserItem";
import Link from 'next/link'

const UsersList = ({ users }) => {
  return (
    <>
      {users.length === 0 ? (
        <p>no users found</p>
      ) : (
        users.map(user => 
            <Link href='/rrr'><a>
        <UserItem key={user.id} name={user.name} image={user.image} places={user.places} />
        </a>
        </Link>
        )
      )}
    </>
  );
};

export default UsersList;
